/* Estilos para la sección de contacto */
.contacto-section {
    background-color: #f7f7f7;
    padding: 50px 0;
    text-align: center;
}

.contacto-content {
    max-width: 800px;
    margin: 0 auto;
}

.contacto-content h2 {
    font-size: 2rem;
    margin-bottom: 20px;
}

.contacto-content p {
    font-size: 1rem;
    margin-bottom: 30px;
}

.contacto-form {
    text-align: left;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group textarea {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
}

.form-group textarea {
    resize: vertical;
    height: 100px;
}

.enviar-button {
    background-color: #FF5733;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 15px 30px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.enviar-button:hover {
    background-color: #FF2E00;
}
